@media only screen and (min-width:100px) and (max-width: 1140px) {
    .astro{

       display:none;

  }
  .form{
      width:100%;
  }
  
}
/* examples/custom-animation.css */
@keyframes customEnterAnimation {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes customLeaveAnimation {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

